body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

header {
  width: 100%;
  height: 14vh;
  max-height: 105px;
  display: flex;
  align-items: center; }
  @media only screen and (min-width: 768px) {
    header {
      height: 11vh;
      max-height: none; } }

.app-title {
  font-size: 1.4em;
  margin: auto; }
  @media only screen and (min-width: 768px) {
    .app-title {
      width: 100%;
      font-size: 2em; } }

.app-title span {
  font-family: 'Bungee Inline', cursive;
  font-weight: 100;
  color: #48605A;
  border: #48605A dashed 4px;
  border-radius: 4px;
  padding: 0.15em; }

.infowindow {
  padding: 0;
  margin: 0;
  color: black; }
  @media only screen and (min-width: 768px) {
    .infowindow {
      width: 240px;
      height: 116px; } }

.infowindow h3 {
  margin: 6px; }

.attraction-info {
  display: flex;
  padding-top: 4px; }

.attraction-img {
  width: 35%;
  height: 100%;
  border-radius: 8px; }

.bulb-logo {
  color: #48605A;
  font-size: 2em; }

.attraction-tip {
  width: 65%;
  text-align: left;
  margin: 0;
  padding-left: 10px;
  font-size: 0.9em; }
  @media only screen and (min-width: 768px) {
    .attraction-tip {
      font-size: 1em; } }

footer {
  display: none; }
  @media only screen and (min-width: 768px) {
    footer {
      position: absolute;
      bottom: 0;
      color: #D1D7DCFF;
      /*left: 0;*/
      width: inherit;
      padding-bottom: 6px;
      margin: auto;
      width: 100%;
      display: block; } }

footer p {
  font-size: 10px;
  padding: 0;
  margin: 0 auto; }

.fs-logo {
  color: #F94879; }

* {
  box-sizing: border-box; }

.app {
  text-align: center;
  height: 100vh;
  background-color: #8AA59E;
  color: #EFF5F5;
  font-family: 'Open Sans', sans-serif;
  font-weight: normal; }

main {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
  height: calc(100vh - 14vh); }

.sidebar {
  width: 100%;
  overflow-y: auto;
  flex: 1 1; }

.mapDiv {
  width: 100%;
  height: 43vh; }

/*!important is used to override inline styles for google-maps-react*/
.mapDiv > div[style] {
  width: inherit !important;
  height: inherit !important; }

.attractions-select {
  background-color: white;
  border: 1px solid #fff;
  font-size: 1.1em;
  margin: 5px;
  padding: 0 2px;
  width: calc(100% - 15px);
  outline-color: #48605A; }

.attractions-select option {
  overflow: hidden;
  text-overflow: ellipsis;
  color: green; }

.attractions-list {
  list-style: outside none none;
  text-align: left;
  font-size: 1.1em;
  margin: 0;
  padding: 8px; }

.attractions-list li {
  margin: 8px 0;
  padding: 5px;
  border-width: 0;
  font-size: 0.9em;
  transition: border-width 0.8s linear; }

.attractions-list li:hover {
  cursor: pointer; }

.attractions-list li:focus {
  outline: none;
  border: #EFF5F5 solid 2px;
  border-radius: 4px;
  transition: border-width 0.8s linear; }

.cat-logo {
  vertical-align: middle;
  padding: 0;
  padding-right: 0.2em; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

.address-hide {
  font-size: 0.75em;
  padding-left: 1.2em;
  padding-top: 0.4em;
  margin: 0;
  -webkit-animation: fadeOut 1.25s;
  animation: fadeOut 1.25s;
  display: none; }

.address-show {
  font-size: 0.75em;
  padding-left: 1.2em;
  padding-top: 0.4em;
  margin: 0;
  display: block;
  -webkit-animation: fadeIn 1.25s;
  animation: fadeIn 1.25s; }

.marker:hover {
  cursor: pointer; }

.gmap-fail {
  text-align: justify;
  width: 100%;
  height: inherit;
  padding: 3em;
  background-color: white;
  color: #48605A; }

@media only screen and (min-width: 768px) {
  .app {
    display: flex;
    flex-wrap: wrap; }
  main {
    flex-direction: row;
    height: calc(100vh - 11vh); }
  .mapDiv {
    width: calc(100% - 35%);
    height: calc(100vh - 11vh); }
  .sidebar {
    width: 35%;
    height: inherit;
    flex: 0 1 auto;
    display: block;
    position: relative; }
  .attractions-list li {
    font-size: 1em; }
  .cat-logo {
    height: 1.4em;
    vertical-align: middle; }
  .address-hide, .address-show {
    padding-left: 1.6em; } }

@media only screen and (min-width: 992px) {
  .sidebar {
    width: 30%; }
  .mapDiv {
    width: calc(100% - 30%); } }

